.MainNavbar{
    display: flex;
    width: 100%;
    height: 55px;
    /* border: 1px solid red; */
    justify-content: flex-end;
    align-items: center;
    background-color: var(--purple);
    box-sizing: border-box;
    position: fixed;
    top: 0%;
    left: 0%;
    z-index: 10;
    margin-bottom: 100px;
}

.NavbarHolder{
    display: flex;
    gap: 10px;
    justify-content: flex-end;
    /* border: 1px solid red; */
    align-items: center;
    background-color: var(--purple);
    font-size: 18px;
    width: 50%;   
}

.NavbarHolder a{
    color: var(--sky-blue);
    padding: 10px;
    border-radius: 10px;
}

.NavbarHolder a:hover{
    background: var(--dark-blue);
}

.NavbarHolder_display{
    display: none;
}

.button_float{
    display: none;
}

.btn{
    background-color: var(--dark-blue);
    color: var(--sky-blue);
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 10px;
    margin-top: 2px;
}

.active{
    background-color: var(--dark-blue);
    border-radius: 10px;
}

/* medium screen */
@media screen and (min-width:600px) and (max-width:800px) {
    .NavbarHolder{
        gap: 5px;
        font-size: 14px;
    }
}


/* small screen */
@media screen and (min-width:0px) and (max-width:600px) {

    .button_float{
        display: block;
        padding: 10px;
        background: var(--light-blue);
        /* font-weight: 700; */
        border-radius: 10px;
        margin-right: 5px;
    }

    .NavbarHolder{
        display: none;
    }
    .NavbarHolder_display{
        /* border: 1px solid red; */
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        background-color: var(--purple);
        position: absolute;
        /* top: -500%; */
        padding: 2px;
        padding-inline: 10px;
        z-index: 10;
        width: 100vw;
        height: 55vh;
        transition: .5s;
    }
    .NavbarHolder_display a{
        color: var(--sky-blue);
        padding: 7px;
    }
    
    
}
